import { ConstructorFragment } from "ethers/lib/utils";

// TODO: Could you fetch these online? 
// or make an array here?
// https://chainlist.org/
 export function getNetworkByChainId (chainId) {
  switch(chainId) {
    case 1:     return { id: "ethereum",            chainId: chainId, currency: 'ETH' };
    // 2: expanse 
    case 3:     return { id: "ropsten",             chainId: chainId, currency: 'ETH' };
    // 4: rinkeby
    // 5: grli
    case 56:    return { id: "binanceSmartChain",   chainId: chainId, currency: 'BNB' };

    // TODO: hardhat fix
    case 1337:  return { id: "localhost",           chainId: chainId, currency: 'ETH' };

    default: throw new Error(`chainId '${chainId}' not supported`)
  }
}

