import {
  BrowserRouter,
  Route,
  Link
} from 'react-router-dom'
import { Navbar, Nav, Button, Container } from 'react-bootstrap'
import market from './marketplace.png'

export const Navigation = ({ web3Handler, account }) => {
  return (
    <Navbar expand="lg" bg="secondary" variant="dark">
      <Container>
        <Navbar.Brand href="https://github.com/saku-kaarakainen">
          <img src={market} with='40' height='40' className='' alt='Marketplace logo' />
            &nbsp; My NFT Marketplace
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsibe-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/create">Create</Nav.Link>
            <Nav.Link as={Link} to="/my-listed-items">My Listed Items</Nav.Link>
            <Nav.Link as={Link} to="/my-purchases">My Purchases</Nav.Link>
          </Nav>
          <Nav>
            {account 
              ? (<Nav.Link 
                  href={`https://etherscan.io/address/${account}}`}
                  target="_blank"
                    rel="noopener noreferer"
                  className="button nav-button button-sm mx-4">
                    <Button variant="outline-ligth">
                      {account.slice(0, 5) + '...' + account.slice(38, 42)}
                    </Button>
                  </Nav.Link>) 
              : (<Button onClick={web3Handler} variant="outline-light">Connect wallet</Button>)}
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  )
}