import logo from "./marketplace.png";
import "./App.css";
import React, { useState } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { ethers } from "ethers";
import { Spinner } from "react-bootstrap";

// Components
import { Navigation } from "./Navbar";
import { Home } from "./Home";
import { Create } from "./Create";
import { MyListedItems } from "./MyListedItems";
import { MyPurchases } from "./MyPurchases";
import { getNetworkByChainId } from './../utils/networkUtils'

// Get Contracts
const getMarketplaceAbi      = (network) => require(`./../contractsData/${network}/Marketplace.json`)
const getMarketplaceAddress  = (network) => require(`./../contractsData/${network}/Marketplace-address.json`)
const getNFTAbi              = (network) => require(`./../contractsData/${network}/NFT.json`)
const getNFTAddress          = (network) => require(`./../contractsData/${network}/NFT-address.json`)

function App() {
  const [loading, setLoading] = useState(true);
  const [account, setAccount] = useState(null);
  const [nft, setNFT] = useState({});
  const [marketplace, setMarketplace] = useState({});

  // MetaMask Login/Connect
  const web3Handler = async () => {
    const accounts = await window.ethereum.request({
      method: "eth_requestAccounts",
    });
    setAccount(accounts[0]);
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();

    const { chainId } = await provider.getNetwork();
    const network =  getNetworkByChainId(chainId) 

    console.log("account: ", accounts[0])
    console.log('network: ', network)

    loadContracts(signer, chainId);
  };

  const loadContracts = async (signer, network) => {
    const MarketplaceAbi      = getMarketplaceAbi      (network.id);
    const MarketplaceAddress  = getMarketplaceAddress  (network.id);
    const NFTAbi              = getNFTAbi              (network.id);
    const NFTAddress          = getNFTAddress          (network.id);


    // Get deployed contracts
    const marketplace = new ethers.Contract(
      MarketplaceAddress.address,
      MarketplaceAbi.abi,
      signer
    );
    setMarketplace(marketplace);

    const nft = new ethers.Contract(NFTAddress.address, NFTAbi.abi, signer);
    setNFT(nft);
    setLoading(false);
  };

  return (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <div className="App">
        <Navigation web3Handler={web3Handler} account={account} />
        {loading ? (
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              minHeight: "80vh",
            }}
          >
            <Spinner animation="border" style={{ display: "flex" }} />
            <p className="mx-3 my-0">Awaiting Metmask connection...</p>
          </div>
        ) : (
          <Routes>
            <Route
              path="/"
              element={<Home marketplace={marketplace} nft={nft} />}
            />
            <Route
              path="/create"
              element={<Create marketplace={marketplace} nft={nft} />}
            />
            <Route path="/my-listed-items" element={<MyListedItems />} />
            <Route path="/my-purchases" element={<MyPurchases />} />
          </Routes>
        )}
      </div>
    </BrowserRouter>
  );
}

export default App;
